<template>
	<div class="app">
		<form action="/">
			<van-search v-model="value" show-action placeholder="请输入搜索关键词" @search="searchkey" @input="onSearch"
				@cancel="goback" />
		</form>
		<div class="onsearchbox" v-if="searchlist.length===0">
			<div class="nav" v-show="list.length===0">
				<div class="nav-left" style="margin-left: 10px;">
					<p>搜索历史</p>
				</div>
				<div class="nav-right" @click="del">
					<div class="nav-text">
						<p>全部删除</p>
					</div>
					<van-icon name="delete-o" />
				</div>
			</div>
			<div class="nav-list" v-show="list.length===0" style="background-color: #fff;">
				<ul>
					<li v-for="(item,i) in search_history" :key="search_history.id" v-if="search_history.length>0">
						<div class="nav-box" style="display: flex;align-items: center;">
							<p @click="todetails(item)">{{item.title}}</p>
							<van-icon name="cross" style="text-align: center;margin-left: 10px;" @click="cancel(i)" />
						</div>

					</li>
				</ul>
			</div>
			<div class="list" v-show="list.length>0">
				<ul>
					<li v-for="item in list" :key='item.id'>
						<p @click="todetails(item)" style="word-wrap: break-word; text-align: left;">
							{{item.title}}
						</p>
						<!-- <van-icon name="close" style="flex: 1;" /> -->
					</li>
				</ul>
			</div>
		</div>

		<div class="searchlist" v-else style="background-color: #fff;">
			<ul>
				<li v-for="item in searchlist" :key="item.id" style="color: #000;" @click="todetails(item)">
					<div class="top">
						<!-- 标题 -->
						<p style="word-wrap: break-word;">{{item.title}}</p>
						<!-- 图片 -->
						<div class="img" style="width:110px;height: 100px;" v-show="item.imageSrc.length<3">
							<img :src="item.imageSrc[0]" v-show="item.imageSrc.length"
								style="margin-top: 10px;width: 100%;height: 100%;object-fit: cover;">
						</div>
					</div>
					<!-- 多图片展示 -->
					<div class="box" v-show="item.imageSrc.length===3"
						style="display: flex;width: 90vw;justify-content: space-around;">
						<div class="imgs" v-for="item in item.imageSrc" style="margin: 5px;width:150px;height: 80px;">
							<img :src="item" :key='item.id'
								style="margin-top: 10px;width: 100%;height: 100%;object-fit: cover;">
						</div>
					</div>
					<div class="bottom">
						<span class="van-ellipsis" style="flex: 4;">{{item.author}} </span>
						<div class="bottom-right">
							<span>{{item.comment}}评论</span>
							<span> 发布于{{new Date(item.time).toLocaleDateString()}}</span>
						</div>
					</div>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	import {
		List,
		Toast,
	} from 'vant';
	export default {
		components: {
			[Toast.name]: Toast,
		},
		data() {
			return {
				name: 'searchs',
				value: '',
				list: [],
				records: '',
				search_history: [],
				searchlist: []
			}
		},
		created() {
			let search_history = localStorage.getItem('search_history')
			if (search_history) {
				this.search_history = JSON.parse(search_history)
			}
		},
		methods: {
			// 搜索
			onSearch(val) {
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/search', {
					key_word: val,
					skip: 0,
					limit: 10
				}).then(res => {
					// console.log(res.data);
					if (res.data.code === 0) {
						this.list = res.data.data
					}
					if (res.data.code === 20030) {
						this.list = []
					}
					if (res.data.count === 0) {
						Toast('未搜索到相关内容')
					}
				})
			},
			searchkey(val) {
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/search', {
					key_word: val,
					skip: 0,
					limit: 10
				}).then(res => {
					console.log(res.data);
					if (res.data.code === 0) {
						this.searchlist = res.data.data
						this.list = []
						this.value = ''
					}
					if (res.data.code === 20030) {
						this.searchlist = []
					}
					if (res.data.count === 0) {
						Toast('未搜索到相关内容')
					}
				})
			},
			// 点击前往
			todetails(item) {
				// 留下记录
				let index = this.search_history.findIndex(v => v.title === item.title);
				if (index >= 0) {
					this.search_history.splice(index, 1)
				}
				this.search_history.unshift({
					title: item.title,
					timestamp: +new Date(),
					_id: item._id
				})
				localStorage.setItem('search_history', JSON.stringify(this.search_history))
				// 路由跳转
				this.$router.push({
					path: '/details',
					query: {
						article_id: item._id
					}
				})
			},
			// 全部删除
			del() {
				this.search_history = []
				localStorage.removeItem('search_history')
			},
			// 删除记录
			cancel(i) {
				this.search_history.splice(i, 1)
				localStorage.setItem('search_history', JSON.stringify(this.search_history))
			},
			// 返回上一级
			goback() {
				if (this.searchlist == '') {
					this.$router.push('/')
				}
				if (this.searchlist != []) {
					this.searchlist = []
				}
			}
		},

	}
</script>

<style lang="less" scoped>
	.app {
		height: 100vh;
		background-color: #f1f1f1;
	}

	.nav {
		padding-top: 10px;
		display: flex;
		justify-content: space-between;
		background-color: #fff;
	}

	.nav>p {
		padding-left: 15px;
	}

	.nav-right {
		display: flex;
		margin-right: 15px;
		align-items: stretch;
	}

	.nav-text {
		display: flex;
		text-align: right;
	}

	.van-icon {
		padding: 5px;
	}

	.list li {
		margin-top: 1px;
		background-color: #fff;
		align-items: center;
		padding: 15px;
	}

	.nav-list>ul {
		text-align: left;
		margin-left: 10px;
	}

	.nav-list>ul>li {
		display: inline-block;
		font-size: 3.46667vmin;
		// height: 9.06667vmin;
		line-height: 8.8vmin;
		padding: 0 3.2vmin;
		color: #505050;
		border: 1px solid #ccc;
		border-radius: 4.53333vmin;
		margin-right: 2.66667vmin;
		margin-top: 3.2vmin;
		margin-bottom: 10px;
	}

	.searchlist>ul>li {
		display: flex;
		width: 90%;
		height: 200px;
		margin: 0 auto;
		flex-direction: column;
		align-items: flex-start;
		border-bottom: 1px solid #ccc;
		justify-content: space-between;
		padding-left: 10px;
		padding-bottom: 5px;
		padding-top: 5px;
	}

	.top {
		display: flex;
	}

	.top p {
		width: 60vw;
		text-align: left;
		margin-top: 10px;
	}

	.bottom {
		width: 100%;
		text-align: left;
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
	}
</style>
