<template>
	<div>
		<!-- 标题 -->
		<div>
			<van-nav-bar title="注册 / 登录" left-arrow @click-left="onClickLeft" />
		</div>
		<!-- 登录 -->
		<van-form @submit="onSubmit" v-show="key">
			<div class="phone">
				<van-icon name="phone-o" />
				<van-field v-model="username" name="username" placeholder="请输入手机号"
					:rules="[{ required: true, message: '请输入手机号' }]" />
			</div>
			<div class="password">
				<van-icon name="eye-o" />
				<van-field v-model="password" type="password" name="password" placeholder="请输入密码"
					:rules="[{ required: true, message: '请输入密码' }]" />
			</div>
			<div class="text" style="display: flex;justify-content: space-between;">
				<p>还没有注册吗？去 <a @click="change">注册</a></p>
				<p style="color: #c1080b;" @click="warn">忘记密码？</p>
			</div>
			<div style="margin: 16px;">
				<van-button round block type="info" native-type="submit" color="#07c160">登录
				</van-button>
			</div>
		</van-form>
		<!--  注册 -->
		<van-form v-show='key2'>
			<div class="phone">
				<van-icon name="phone-o" />
				<van-field v-model="username" name="username" placeholder="请输入手机号"
					:rules="[{ required: true, message: '请输入手机号' }]" />
			</div>
			<div class="password">
				<van-icon name="eye-o" />
				<van-field v-model="password" type="password" name="password" placeholder="请输入密码"
					:rules="[{ required: true, message: '请输入密码' }]" />
			</div>
			<div class="primary">
				<van-field v-model="sms" center clearable label="短信验证码" placeholder="请输入短信验证码">
					<template #button>
						<van-button size="small" type="primary" @click='onprimary'>发送验证码</van-button>
					</template>
				</van-field>
			</div>
			<div class="text" style="display: flex;justify-content: space-between;">
				<p>已经有账号了？去 <a @click="change2">登录</a></p>
				<p style="color: #c1080b;" @click="warn">忘记密码？</p>
			</div>
			<div style="margin: 16px;">
				<van-button round block type="info" native-type="submit" color="#07c160" @click='enroll'>注册
				</van-button>
			</div>
		</van-form>
		<!-- 忘记密码 -->
		<!-- <div class="warnpass" style="margin-top: 20px;"> -->
		<van-form v-show="warns">
			<div class="phone">
				<van-icon name="phone-o" />
				<van-field v-model="username" name="username" placeholder="请输入手机号"
					:rules="[{ required: true, message: '请输入手机号' }]" style="align-items: flex-end;" />
			</div>
			<div class="password">
				<van-icon name="eye-o" />
				<van-field v-model="warnpassword" type="password" name="warnpassword" placeholder="请输入新密码"
					:rules="[{ required: true, message: '请输入新密码' }]" />
			</div>
			<div class="primary">
				<van-field v-model="sms" center clearable label="短信验证码" placeholder="请输入短信验证码">
					<template #button>
						<van-button size="small" type="primary" @click='onwarns'>发送验证码</van-button>
					</template>
				</van-field>
			</div>
			<div class="text" style="display: flex;justify-content: space-between;">
				<p>已经有账号了？去 <a @click="change2">登录</a></p>
				<p style="color: #c1080b;" @click="warn">忘记密码？</p>
			</div>
			<div style="margin: 16px;display: flex;justify-content: space-evenly;">
				<van-button round block type="info" native-type="submit" color="#07c160" @click='onWarp'>确定
				</van-button>
			</div>
		</van-form>
		<!-- </div> -->
	</div>
</template>

<script>
	import axios from 'axios';
	import store from '@/store/index'
	import {
		Form,
		Field,
		Toast
	} from 'vant';
	export default {
		components: {
			store,
			[Form.name]: Form,
			[Field.name]: Field,
			[Toast.name]: Toast,
		},
		data() {
			return {
				username: '',
				password: '',
				sms: '',
				key: true,
				key2: false,
				name: 'Login',
				warns: false,
				warnpassword: ''
			};
		},
		methods: {
			// 返回上一级路由
			onClickLeft() {
				this.$router.push('/my')
			},
			// 注册登录切换
			change() {
				this.key = false
				this.key2 = true
				this.warns = false
			},
			change2() {
				this.key = true
				this.key2 = false
				this.warns = false
			},
			// 提交登录
			onSubmit() {
				// console.log(this.username);
				// console.log(this.password);
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/login',
					data: {
						username: this.username,
						password: this.password
					}
				}).then(res => {
					console.log(res.data);
					console.log(res.data.msg);
					if (res.data.msg == "登录成功") {
						localStorage.clear();
						let userInfo = {
							isLogin: true,
							uid: res.data.uid,
							birthday: res.data.userInfo.birthday,
							avatar: res.data.userInfo.avatar,
							sex: res.data.userInfo.sex,
							id: res.data.userInfo._id,
							nickname: res.data.userInfo.nickname,
							token: res.data.token,
							username: res.data.username
						};
						window.localStorage.setItem("userInfo", JSON.stringify(userInfo));
						this.$store.state.isLogin = userInfo.isLogin
						console.log('this.$store.state.isLogin', this.$store.state.isLogin)
						this.$router.push('/my')
					}
				}, err => {
					console.log(err);
				})
			},
			// 获取验证码
			onprimary() {
				if (this.username == '') {
					Toast('手机号不能为空');
				}
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/sendSms',
					data: {
						mobile: this.username,
						type: 'register'
					}
				}).then(res => {
					if (res.data.code === 1) {
						Toast(res.data.msg)
					}
					console.log(res.data);
					this.sms = res.data.data
				})

			},
			// 注册
			enroll() {
				if (this.sms == '' || this.sms.length != 6) {
					Toast('验证码格式错误')
				}
				axios({
					method: 'post',
					url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/reg',
					data: {
						username: this.username,
						password: this.password,
						vercode: this.sms
					}
				}).then(res => {
					console.log(res.data);
					// this.sms = res.data.data
				}).catch(err => {
					console.log(err.data);
				})
			},
			// 忘记密码
			warn() {
				this.warns = true
				this.key = false
				this.key2 = false
			},
			onWarp() {
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/forget', {
					username: this.username,
					password: this.warnpassword,
					vercode: this.sms
				}).then(res => {
					// Toast(res.data)
					console.log(res.data);
					if (res.data.code == 50201) {
						Toast('手机号未输入或不合法')
						return;
					}
					if (res.data.code == 50202) {
						Toast('验证码或密码错误')
						return;
					}
					if (res.data.code == 0) {
						Toast(res.data.msg + "请重新登录")
						localStorage.clear();
						let userInfo = {
							isLogin: false
						};
						window.localStorage.setItem("userInfo", JSON.stringify(userInfo));
						this.$store.state.isLogin = userInfo.isLogin
						setTimeout(() => {
							this.$router.push({
								path: '/'
							}, () => {}, () => {}); //退出登录后1秒后跳转至首页
						}, 1000)
					}
				})
			},
			// 获取忘记密码的验证码
			onwarns() {
				if (this.username == '') {
					Toast('手机号不能为空');
				} else {
					axios({
						method: 'post',
						url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/sendSms',
						data: {
							mobile: this.username,
							type: 'login'
						}
					}).then(res => {
						console.log(res.data);
						this.sms = res.data.data
					})
				}
			},

		},
		mounted() {

		}
	}
</script>

<style lang="less" scoped>
	/deep/ .van-nav-bar {
		background-color: #07c160;
	}

	/deep/.van-nav-bar .van-icon {
		color: #fff;
	}

	/deep/ .van-nav-bar__title {
		color: #fff;
	}

	.phone,
	.password {
		display: flex;
	}


	.van-from {
		// position: relative;
	}

	.van-icon {
		// position: absolute;
		// left: 10px;
		// top: 50px;
		// // display: block;
		// width: 50px;
		// height: 50px;
		width: 10%;
		// text-align: center;
		margin-top: 13px;
	}

	.text {
		text-align: left;
		margin-left: 10px;
		font-size: 12px;
	}

	.text a {
		color: #c1080b;
	}
</style>
