<template>
	<div id="app">
		<router-view />
		<van-tabbar v-model="active" v-if="mainPage" active-color="#ee0a24" route>
			<van-tabbar-item replace to="/" icon="home-o">首页</van-tabbar-item>
			<van-tabbar-item replace icon="add-o" @click="onof">发布</van-tabbar-item>
			<van-tabbar-item replace icon="manager-o" to="/my">我的</van-tabbar-item>
		</van-tabbar>
	</div>
</template>

<script>
	import home from '@/views/homepage/home.vue';
	import {
		Toast
	} from 'vant';
	export default {
		data() {
			return {
				active: 0,
				value: 1,
			}
		},
		components: {
			home,
		},
		computed: {
			mainPage() {
				return this.$route.meta.root
			}
		},
		methods: {
			// 判断登录状态
			onof() {
				let userInfo = JSON.parse(localStorage.getItem('userInfo'));
				if (null === userInfo) {
					// return
				} else if (userInfo.isLogin) {
					this.$store.state.isLogin = userInfo.isLogin
				}

				// console.log(this.$store.state.isLogin);
				if (this.$store.state.isLogin) {
					this.$router.push('/publish')
				} else {
					this.$router.push('/login')
				}
			},
		},
	};
</script>

<style lang="scss">
	* {
		margin: 0;
		padding: 0;
	}

	#app {
		font-family: Avenir, Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		text-align: center;
		color: #2c3e50;
	}

	.box {
		width: 500px;
	}


	.share-box {
		width: 100%;
		height: 100%;
		position: fixed;
		left: 0;
		top: 0;
		z-index: 999;
		background-color: rgba(0, 0, 0, 0.5);

		.share-picture {
			width: 300px;
			height: 500px;
			position: absolute;
			left: 7%;
			top: 35px;
			padding: 10px;
			// background-color: #fff;

			img {
				width: 100%;
				height: 100%;
				object-fit: contain;
			}

			p {
				width: 180px;
				height: 40px;
				text-align: center;
				border-radius: 15px;
				line-height: 40px;
				margin: 10px auto;
				background-color: #fff;
			}
		}


	}
</style>
