<template>
	<div>
		<div class="comments" style="margin-bottom: 70px;margin-top: 10px;">
			<div class="comment-header">
				<h3>精彩评论 {{comment}}</h3>
				<p>{{like}} 赞 | {{fav}} 收藏</p>
			</div>
			<div class="comment-text">
				<ul>
					<li v-for="item in commentlist" :key="item.id">
						<div class="left">
							<div class="img">
								<img :src="item.info.avatar">
							</div>
							<div class="left-title">
								<p>{{item.info.nickname}}</p>
								<span style="width: 200px;">{{item.content}}</span>
								<div class="answer-list" style="display: flex;flex-direction: column;">
									<span class="answer" @click="changeshow(item)"
										style="margin-left: 20px;margin-top: 10px;">回复</span>
									<van-icon name="arrow-down" @click="getanswer(item)"
										style="position: absolute;left: 140px;margin-top: 12px;" />
									<!-- 回复列表 -->
									<ul v-show="clickitem._id===item._id" style="margin-top: 10px;">
										<li v-for="item in answerlist" :key="item.id">
											<div class="left">
												<div class="img">
													<img :src="item.info.avatar" alt="" srcset="">
												</div>
												<div class="left-title">
													<p>{{item.info.nickname}}</p>
													<span>{{item.content}}</span>
													<div class="answer-list"
														style="display: flex;flex-direction: column;">
														<span class="answer" @click="$toast('这里的区域以后再来探索吧')">回复</span>
													</div>
												</div>
											</div>
											<div class="right">
												<p>{{item.like_count}}
													<van-icon name="good-job" class="icons" v-if="item.is_like"
														color="#ee0a24" @click="cancellikeit(item)" />
													<van-icon name="good-job" class="icons" @click="likeit(item)"
														v-else />
												</p>
												<span>{{new Date(item.create_time).toLocaleDateString()}}</span>
											</div>
										</li>
									</ul>
								</div>
							</div>
						</div>
						<div class="right" style="position: absolute;right: 0;">
							<p>{{item.like_count}}
								<van-icon name="good-job" class="icons" v-if="item.is_like" color="#ee0a24"
									@click="cancellikeit(item)" />
								<van-icon name="good-job" class="icons" @click="likeit(item)" v-else />
							</p>
							<span>{{new Date(item.create_time).toLocaleDateString()}}</span>
						</div>
						<van-popup v-model="show" position="bottom" :style="{ height: '30%' }">
							<van-field v-model="message" rows="2" autosize label="留言" type="textarea" maxlength="50"
								placeholder="请输入留言" show-word-limit style="margin-top: 10px;" />
							<van-button type="primary" size="normal" style="margin-right: 30px;margin-top: 10px;"
								@click="answer">确定</van-button>
							<van-button type="primary" size="normal" @click="show=false">取消</van-button>
						</van-popup>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	import {
		Toast,
		Popup,
		Field
	} from 'vant'
	export default {
		data() {
			return {
				name: 'comment',
				commentlist: '',
				show: false,
				message: '',
				answerlist: '',
				clickitem: '',
				item_id: '',
				key: ''
			}
		},
		components: {
			[Popup.name]: Popup,
			[Field.name]: Field
		},
		props: ['comment', 'fav', 'like', 'uid'],
		methods: {
			// 点赞
			likeit(item) {
				if (this.uid === '') {
					Toast('您还未登录')
				}
				this.key = item._id
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/comment_like', {
					user_id: this.uid,
					comment_id: item._id,
				}).then(res => {
					// console.log(item);
					if (res.data.code === 0) {
						this.getcom()
						setTimeout(() => {
							Toast(res.data.msg);
						}, 500)
					}
				})
			},
			// 取消点赞
			cancellikeit(item) {
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/comment_unlike', {
					user_id: this.uid,
					comment_id: item._id,
				}).then(res => {
					// console.log(res.data);
					if (res.data.code === 0) {
						this.getcom()
						setTimeout(() => {
							Toast(res.data.msg);
						}, 500)
					}
				})
			},
			// 获取评论列表
			getcom() {
				let userInfo = JSON.parse(localStorage.getItem('userInfo'));
				// console.log(this.$store.state.isLogin);
				if (null === userInfo) {
					// return
				} else if (userInfo.isLogin) {
					this.$store.state.isLogin = userInfo.isLogin
				}
				if (this.$store.state.isLogin) {
					axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_comment_list', {
						article_id: this.$route.query.article_id,
						user_id: this.uid
					}).then(res => {
						console.log(res.data.data);
						this.commentlist = res.data.data
					})
				} else if (!this.$store.state.isLogin) {
					axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_comment_list', {
						article_id: this.$route.query.article_id,
					}).then(res => {
						console.log(res.data.data);
						this.commentlist = res.data.data
					})
				}
			},
			// 回复评论
			changeshow(item) {
				this.show = true
				this.item_id = item._id
			},
			answer() {
				if (this.uid === '') {
					Toast('您还未登录')
				}
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_comment', {
					user_id: this.uid,
					article_id: this.$route.query.article_id,
					content: this.message,
					reply_comment_id: this.item_id,
					comment_type: 1
				}).then(res => {
					console.log(this.item_id);
					console.log(res.data);
					if (res.data.code === 0) {
						this.message = ''
						this.show = false
						Toast(res.data.msg)
					}
				})
			},
			// 回复列表
			getanswer(item) {
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_reply_list', {
					skip: 0,
					limit: 10,
					user_id: this.uid,
					reply_comment_id: item._id,
					article_id: this.$route.query.article_id
				}).then(res => {
					console.log(res.data);
					if (res.data.data.length == 0) {
						Toast('这个人还没有回复哦')
					}
					this.answerlist = res.data.data
					this.clickitem = item
				})
			}
		},
		mounted() {
			// 获取评论列表
			this.getcom()
			this.$bus.$on('getcomment', this.getcom)
		}
	}
</script>

<style lang="less" scoped>
	.comment-header {
		display: flex;
		justify-content: space-between;
		margin-left: 10px;
		margin-right: 10px;
		margin-bottom: 10px;
	}

	.comment-text>ul>li {
		display: flex;
		margin-left: 10px;
		margin-bottom: 10px;
		justify-content: space-between;
	}

	.left {
		display: flex;
	}

	.img {
		width: 50px;
		height: 50px;
	}

	.img>img {
		width: 100%;
		height: 100%;
		border-radius: 25px;
	}

	.left-title {
		margin-left: 10px;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
	}

	.left-title>p {
		font-size: 14px;
		font-weight: 700;
	}

	.left-title>span {
		font-size: 12px;
		text-align: left;
		margin-top: 5px;
	}

	.right {
		margin-right: 10px;
	}

	.right>p {
		text-align: right;
		margin-bottom: 10px;
	}

	.answer {
		width: 40px;
		border-radius: 5px;
		background-color: #eee;
		text-align: center !important;
		border: 1px solid #eee;
		font-size: 12px;
		margin-right: 10px;
	}

	.answer-list>ul>li {
		display: flex;
		margin-left: 10px;
		margin-bottom: 10px;
		justify-content: space-between;
		width: 75vw;
	}
</style>
