<template>
	<div class="app">
		<!-- 头部 -->
		<div class="header">
			<div class="top" v-if="isshow">
				<div class="top-left">
					<p class="avatar" style="" v-show="avatar==''">
						<van-icon name="warning-o" />
					<div style="width: 50px;height: 50px;">
						<img :src="avatar" alt="" srcset="" style="width: 100%;height: 100%;border-radius: 50%;">
					</div>
					</p>
					<!-- 名称 -->
					<p style="margin-left: 20px;">{{uname}}</p>
				</div>
				<!-- 编辑资料 -->
				<div class="top-right">
					<router-link to="/user">
						<button>
							编辑资料
						</button>
					</router-link>
				</div>
			</div>
			<div class="tops" style="padding-bottom: 20px;margin-bottom: 30px;" v-else>
				<p style="margin-bottom: 10px;padding-top: 40px;">登录显示更多精彩</p>
				<van-button color="#b91114" round to="/login">登录后更懂你</van-button>
			</div>
			<div class="top-bottom">
				<router-link to="/mylimit">
					<div class="bottom-left">
						<p>{{publish_num}}</p>
						<p>头条</p>
					</div>
				</router-link>
				<div class="bottom-right">
					<p>{{liked_num}}</p>
					<p>获赞</p>
				</div>
			</div>
		</div>
		<!-- 列表 -->
		<div class="nav">
			<van-grid clickable :column-num="2">
				<!-- 收藏 -->
				<van-grid-item icon="star-o" text="收藏" to="/fav" />
				<!-- 历史 -->
				<van-grid-item icon="clock-o" text="历史" to="/historylist" />
			</van-grid>
		</div>
		<div class="list">
			<!-- 修改密码 -->
			<van-cell is-link @click="showPopup">修改密码</van-cell>
			<van-popup v-model="show">
				<van-form @submit="onSubmit">
					<van-field v-model="oldPassword" type="password" name="oldPassword" placeholder="请填写旧密码"
						:rules="[{ required: true, message: '请填写旧密码' }]" />
					<van-field v-model="newPassword" type="password" name="newPassword" placeholder="请填写新密码"
						:rules="[{ required: true, message: '请填写新密码' }]" />
					<div style="margin: 16px;">
						<van-button round block type="info" native-type="submit" style="color: #fff;">提交</van-button>
					</div>
				</van-form>
			</van-popup>
			<van-cell is-link @click="warPopup">忘记密码</van-cell>
			<!-- 忘记密码 -->
			<van-popup v-model="warshow" style="width: 100%;height: 50%;">
				<div class="warnpass" style="margin-top: 20px;">
					<van-form>
						<div class="phone">
							<van-icon name="phone-o" />
							<van-field v-model="username" name="username" placeholder="请输入手机号"
								:rules="[{ required: true, message: '请输入手机号' }]" style="align-items: flex-end;" />
						</div>
						<div class="password">
							<van-icon name="eye-o" />
							<van-field v-model="warnpassword" type="password" name="warnpassword" placeholder="请输入新密码"
								:rules="[{ required: true, message: '请输入新密码' }]" />
						</div>
						<div class="primary">
							<van-field v-model="sms" center clearable label="短信验证码" placeholder="请输入短信验证码">
								<template #button>
									<van-button size="small" type="primary" @click='onprimary'>发送验证码</van-button>
								</template>
							</van-field>
						</div>
						<div style="margin: 16px;display: flex;justify-content: space-evenly;">
							<van-button small type="info" native-type="submit" color="#07c160" @click='onWarp'>确定
							</van-button>
							<van-button small type="info" native-type="submit" color="#07c160" @click='warshow=false'>
								取消
							</van-button>
						</div>
					</van-form>
				</div>
			</van-popup>
			<van-cell is-link>关于我们</van-cell>
		</div>
		<!-- 退出登录 -->
		<div class=" quit">
			<van-button type="primary" block color="#fff" @click="logout" v-show="isshow">退出登录
			</van-button>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	import {
		Popup,
		CellGroup,
		Grid,
		GridItem,
		Form,
		Field,
		Toast
	} from 'vant';
	export default {
		components: {
			[Popup.name]: Popup,
			[CellGroup.name]: CellGroup,
			[Grid.name]: Grid,
			[GridItem.name]: GridItem,
			[Form.name]: Form,
			[Field.name]: Field,
		},
		data() {
			return {
				name: 'my',
				show: false,
				isshow: false,
				warshow: false,
				username: "",
				password: '',
				uname: '',
				token: '',
				oldPassword: '',
				newPassword: '',
				uid: '',
				avatar: '',
				sms: '',
				warnpassword: '',
				publish_num: '',
				liked_num: ''
			}
		},
		methods: {
			// 修改密码
			showPopup() {
				let userInfo = JSON.parse(localStorage.getItem('userInfo'));
				if (userInfo.isLogin) {
					this.show = true;
				} else {
					setTimeout(() => {
						Toast('您还未登录')
					}, 100)
				}
			},
			//忘记密码
			warPopup() {
				let userInfo = JSON.parse(localStorage.getItem('userInfo'));
				if (userInfo.isLogin) {
					this.warshow = true;
				} else {
					setTimeout(() => {
						Toast('您还未登录')
					}, 100)
				}
			},
			// 提交修改密码请求
			onSubmit() {
				// console.log('提交');
				if (this.oldPassword === '' || this.newPassword === '') {
					Toast('请输入密码')
				} else {
					axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/updatePwd', {
						oldPassword: this.oldPassword,
						newPassword: this.newPassword,
						uid: this.uid
					}).then(res => {
						console.log(res.data);
						Toast(res.data.msg)
						localStorage.clear();
						let userInfo = {
							isLogin: false
						};
						window.localStorage.setItem("userInfo", JSON.stringify(userInfo));
						this.$store.state.isLogin = userInfo.isLogin
						setTimeout(() => {
							this.$router.push({
								path: '/'
							}, () => {}, () => {}); //退出登录后1秒后跳转至首页
							Toast('请重新登录')
						}, 1000)
					})
				}
			},
			// 提交忘记密码请求
			onWarp() {
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/forget', {
					username: this.username,
					password: this.warnpassword,
					vercode: this.sms
				}).then(res => {
					// Toast(res.data)
					console.log(res.data);
					if (res.data.code == 50201) {
						Toast('手机号未输入或不合法')
						return;
					}
					if (res.data.code == 50202) {
						Toast('验证码或密码错误')
						return;
					}
					if (res.data.code == 0) {
						Toast(res.data.msg + "请重新登录")
						localStorage.clear();
						let userInfo = {
							isLogin: false
						};
						window.localStorage.setItem("userInfo", JSON.stringify(userInfo));
						this.$store.state.isLogin = userInfo.isLogin
						setTimeout(() => {
							this.$router.push({
								path: '/'
							}, () => {}, () => {}); //退出登录后1秒后跳转至首页
						}, 1000)
					}
				})
			},
			// 获取验证码
			onprimary() {
				if (this.username == '') {
					Toast('手机号不能为空');
				} else {
					axios({
						method: 'post',
						url: 'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/sendSms',
						data: {
							mobile: this.username,
							type: 'login'
						}
					}).then(res => {
						console.log(res.data);
						this.sms = res.data.data
					})
				}
			},
			//退出登录
			logout() {
				// this.$store.state.userInfo = null
				localStorage.clear();
				console.log(window.localStorage);
				let userInfo = {
					isLogin: false
				};
				window.localStorage.setItem("userInfo", JSON.stringify(userInfo));
				this.$store.state.isLogin = userInfo.isLogin
				setTimeout(() => {
					this.$router.push({
						path: '/'
					}, () => {}, () => {}); //退出登录后1秒后跳转至首页
				}, 1000)
			},
		},
		created() {
			// 检测登录状态
			let userInfo = JSON.parse(localStorage.getItem('userInfo'));
			// console.log('userInfo', userInfo);
			if (null === userInfo) return
			if (userInfo.isLogin) {
				this.isshow = false
			};
			if (userInfo.isLogin) {
				this.isshow = true
				// 获取信息
				this.uname = userInfo.nickname;
				this.token = userInfo.token;
				this.uid = userInfo.uid;
				this.avatar = userInfo.avatar
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/getuserInfo', {
						token: this.token
					})
					.then(res => {
						console.log(res.data);
						this.publish_num = res.data.publish_num
						this.liked_num = res.data.liked_num
					})
			}
		}
	}
</script>

<style lang="less" scoped>
	.app {
		width: 100%;
		height: 100vh;
		background-color: #eeeeee;
	}

	.header {
		background-color: #07c160;
	}

	.header p {
		color: #fff;
	}

	.avatar {
		width: 50px;
		height: 50px;
		border-radius: 25px;
		background-color: #fff;
		position: relative;
	}

	.avatar>.van-icon {
		position: absolute;
		top: 35%;
		left: 35%;
	}

	.top {
		padding-top: 20px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-bottom: 45px;
	}

	.top-left {
		display: flex;
		align-items: center;
		margin-left: 20px;
	}

	.top-right button {
		width: 70px;
		// height: 30px;
		font-size: 12px;
		border-radius: 10px;
		background-color: #fff;
		color: #000;
		border: none;
		margin-right: 20px;
	}

	.top-bottom {
		display: flex;
		justify-content: space-around;
		padding-bottom: 10px;
	}

	.van-button__text {
		color: red;
	}

	.tops .van-button__text {
		color: #fff;
	}

	.nav {
		margin-bottom: 5px;
	}

	.list {
		margin-bottom: 5px;
	}

	/deep/.van-icon {
		color: #ee0a24;
	}

	.phone,
	.password {
		display: flex;
	}

	.van-icon {
		width: 10%;
		margin-top: 13px;
	}

	.text {
		text-align: left;
		margin-left: 10px;
		font-size: 12px;
	}

	.van-cell {
		align-items: flex-end;
	}
</style>
