<template>
	<div>
		<div>
			<van-nav-bar title="我的发布" left-arrow @click-left="onClickLeft" />
		</div>
		<div class="lists" style="margin-top: 50px;">
			<ul>
				<li v-for="item in list" :key="item.id" style="color: #000;">
					<div class="top" @click="todetails(item)">
						<p style="word-wrap: break-word;">{{item.title}}</p>
						<div class="img" style="width:110px;height: 100px;">
							<img :src="item.imageSrc[0]" v-show="item.imageSrc.length"
								style="margin-top: 10px;width: 100%;height: 100%;object-fit: cover;">
						</div>
					</div>
					<div class="bottom">
						<span style="margin-left: 10px;">{{item.author}} </span>
						<div class="bottom-right">
							<span>{{item.comment}}评论</span>
							<span> 发布于{{new Date(item.time).toLocaleDateString()}}</span>
							<van-icon name="cross" style="margin-left: 40px;margin-right: 10px;"
								@click="dellimit(item)" />
						</div>
					</div>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	import {
		Toast
	} from 'vant';
	export default {
		data() {
			return {
				name: 'mylimit',
				list: '',
				uid: ''
			}
		},
		methods: {
			onClickLeft() {
				this.$router.push('/my')
			},
			dellimit(item) {
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/del_user_article', {
					_id: item._id,
					uid: this.uid
				}).then(res => {
					console.log(res.data);
					if (res.data.msg == '删除成功') {
						this.list = ''
						axios.get(
							`https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_user_article_list?uid=${this.uid}&skip&limit`
						).then(res => {
							console.log(res.data);
							this.list = res.data.data
							Toast('删除' + res.data.msg)
						})
					}
				})
			},
			todetails(item) {
				// console.log(item);
				// return
				this.$router.push({
					path: '/details',
					query: {
						article_id: item._id
					}
				})
			}
		},
		created() {
			let userInfo = JSON.parse(localStorage.getItem('userInfo'));
			this.uid = userInfo.uid
			if (!userInfo.isLogin) {
				Toast('请先登录')
			}
			axios.get(
				`https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_user_article_list?uid=${userInfo.uid}&skip&limit`
			).then(res => {
				console.log(res.data);
				this.list = res.data.data
			})
		}
	}
</script>

<style lang="less" scoped>
	.van-nav-bar {
		background-color: #07c160;
	}

	.van-nav-bar {
		width: 100%;
		position: fixed;
		left: 0;
		top: 0;
	}

	/deep/ .van-nav-bar__title {
		color: #fff;
	}

	/deep/ .van-nav-bar .van-icon {
		color: #fff;
	}

	li {
		display: flex;
		flex-direction: column;
		border-bottom: 1px solid #eee;
	}

	.top {
		display: flex;
		justify-content: space-around;
		height: 120px;
		margin-top: 5px;
		margin-bottom: 10px;
	}

	.top>p {
		flex: 6;
		text-align: left;
		margin-left: 10px;
		margin-top: 10px;
	}

	.top .img {
		flex: 4;
		margin-right: 10px;
		margin-bottom: 10px;
	}

	.bottom {
		display: flex;
		justify-content: space-between;
		margin-bottom: 10px;
	}
</style>
