<template>
	<div>
		<div class="pinfo">
			<ul class="tableList">
				<van-pull-refresh v-model="refreshing" @refresh="onRefresh">
					<template #pulling="props">
						<img class="doge" src="@/assets/doge.png"
							:style="{ transform: `scale(${props.distance / 80})` }" />
					</template>

					<!-- 释放提示 -->
					<template #loosing>
						<img class="doge" src="@/assets/doge.png" />
					</template>

					<!-- 加载提示 -->
					<template #loading>
						<img class="doge" src="@/assets/doge-fire.jpg" />
					</template>
					<van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad"
						style="margin-top: 25px;">
						<!-- <router-link to="/details"> -->
						<li v-for="item in centerlist" :key="item.id" style="color: #000;height: 200px;"
							@click="todetails(item)">
							<div class="top">
								<!-- 标题 -->
								<p style="word-wrap: break-word;">{{item.title}}</p>
								<!-- 图片 -->
								<div class="img" style="width:110px;" v-show="item.imageSrc.length<3">
									<img :src="item.imageSrc[0]" v-show="item.imageSrc.length"
										style="margin-top: 10px;width: 100%;object-fit: cover;max-height: 160px;">
								</div>
							</div>
							<!-- 多图片展示 -->
							<div class="box" v-show="item.imageSrc.length===3"
								style="display: flex;width: 90vw;justify-content: space-around;">
								<div class="imgs" v-for="item in item.imageSrc" style="margin: 5px;width:150px;">
									<img :src="item" :key='item.id'
										style="margin-top: 10px;width: 100%;max-height: 120px; object-fit: cover;">
								</div>
							</div>
							<div class="bottom">
								<span class="van-ellipsis" style="flex: 4;">{{item.author}} </span>
								<div class="bottom-right">
									<span>{{item.comment}}评论</span>
									<span> 发布于{{new Date(item.time).toLocaleDateString()}}</span>
								</div>
							</div>
						</li>
						<!-- </router-link> -->
					</van-list>
				</van-pull-refresh>
			</ul>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import {
		Image as VanImage,
		List,
	} from 'vant';
	export default {
		components: {
			[VanImage.name]: VanImage,
			[List.name]: List,
		},
		data() {
			return {
				centerlist: [],
				list: [],
				loading: false,
				finished: false,
				refreshing: false,
				skip: 0,
				limit: 10,
			}
		},
		methods: {
			onLoad(creatId) {
				setTimeout(() => {
					axios({
						method: 'post',
						url: "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_list",
						data: {
							"cate_id": creatId ? creatId : "609e99d6935e5b0001eaf4f2",
							"skip": this.skip,
							"limit": this.limit
						}
					}).then((data) => {
						// console.log(data.data);
						this.centerlist = data.data.data
						console.log(this.centerlist);
						// this.article_id = this.centerlist.article_id
						this.refreshing = false;
						this.loading = false;
						this.skip = this.centerlist.length;
						this.limit += 10;
						if (this.limit > 40) {
							this.finished = true;
						}
					})
				}, 1000);
			},
			onRefresh() {
				// 清空列表数据
				this.finished = false;
				// 重新加载数据
				// 将 loading 设置为 true，表示处于加载状态
				this.loading = true;
				this.onLoad();
			},
			getData(creatId) {
				axios({
					method: 'post',
					url: "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_list",
					data: {
						"cate_id": creatId ? creatId : "609e99d6935e5b0001eaf4f2",
						"skip": 0,
						"limit": 10
					}
				}).then((data) => {
					// console.log(data.data.data);
					this.centerlist = data.data.data
				})
			},
			todetails(item) {
				// console.log(item);
				// return
				this.$router.push({
					path: '/details',
					query: {
						article_id: item._id
					}
				})
			}
		},
		mounted() {
			this.getData();
			this.$bus.$on('creatId', this.getData)
		}
	}
</script>

<style lang="less" scoped>
	/deep/ .van-pull-refresh {
		margin-top: 20px;
	}

	/deep/ .van-pull-refresh__track {
		position: static;
	}

	.van-list {
		margin-bottom: 50px;
	}

	.doge {
		width: 140px;
		height: 72px;
		margin-top: 8px;
		border-radius: 4px;
	}

	.pinfo {
		margin-top: 85px;
	}

	.tableList {
		height: 100%;
	}

	.tableList li {
		display: flex;
		width: 90%;
		height: 200px;
		margin: 0 auto;
		flex-direction: column;
		align-items: flex-start;
		border-bottom: 1px solid #ccc;
		justify-content: space-between;
		padding-left: 10px;
		padding-bottom: 5px;
		padding-top: 5px;
	}

	.top {
		display: flex;
	}

	.top p {
		width: 60vw;
		text-align: left;
		margin-top: 10px;
	}

	.bottom {
		width: 100%;
		text-align: left;
		display: flex;
		justify-content: space-between;
		align-items: flex-end;
		margin-bottom: 5px;
	}

	.doge {
		width: 140px;
		height: 70px;
		margin-top: 8px;
		border-radius: 4px;
		object-fit: cover;
	}

	/deep/.van-pull-refresh__head {
		height: 80px;
	}
</style>
