<template>
	<div>
		<!-- 头部 -->
		<div>
			<van-nav-bar title="文章详情" left-arrow @click-left="onClickLeft" />
		</div>
		<!-- 内容 -->
		<div id="details-content">
			<div class="content">
				<!-- 标题 -->
				<h3>{{title}}</h3>
				<div class="user">
					<div class="userinfo" style="display: flex;">
						<!-- 头像 -->
						<div class="img" style="width: 50px;height: 50px;">
							<img :src="avatar" alt="" srcset="" style="width: 100%;height: 100%;border-radius: 50%;">
						</div>
						<!-- 名称 时间 -->
						<div class="info">
							<p class="van-ellipsis" style="width: 50vw;text-align: left;">{{author}}</p>
							<span>{{new Date(1666365377288).toLocaleDateString()}}</span>
						</div>
					</div>
					<!-- 右侧关注按钮 -->
					<div class="infobtn" style="margin-right: 20px;">
						<button class="guanzhu" v-show="ishow" @click="changeshow">+关注</button>
						<button class="guanzhu" v-show="!ishow" @click="changeshow">已关注</button>
					</div>
				</div>
			</div>
			<!-- 内容和图片 -->
			<div class="list" style="border-bottom: 1px solid #eee;">
				<p style="text-align: left;margin-left: 10px;margin-bottom: 10px;word-wrap: break-word;">{{content}}</p>
				<div class="img" style="width: 95%;margin: 0 auto;margin-bottom: 10px;">
					<img :src="item" alt="内容图片" srcset="" style="width: 100%;height: 100%;" v-for="item in imageSrc">
				</div>
			</div>
		</div>

		<!-- 评论列表 -->
		<comment :comment='comment' :fav='fav' :like='like' :uid='uid' :imageSrc='imageSrc'></comment>
		<!-- 底部导航 -->
		<detailsFooter :uid='uid' :item='item' @render='render'>
		</detailsFooter>
	</div>
</template>

<script>
	import axios from 'axios'
	import html2canvas from 'html2canvas'
	import qrcode from 'qrcode'
	import comment from '@/views/article/comment.vue'
	import detailsFooter from '@/views/article/detailsFooter.vue'
	import {
		Toast
	} from 'vant'
	// import {
	// 	image
	// } from 'html2canvas/dist/types/css/types/image'
	export default {
		data() {
			return {
				name: 'details',
				content: '',
				title: '',
				imageSrc: '',
				time: '',
				author: '',
				like: '',
				comment: '',
				fav: '',
				avatar: '',
				ishow: true,
				uid: '',
				item: ''
			}
		},
		components: {
			comment,
			detailsFooter
		},
		methods: {
			onClickLeft() {
				this.$router.back()
			},
			changeshow() {
				this.ishow = !this.ishow
			},
			render() {
				let el = document.getElementById('details-content')
				html2canvas(el, {
					allowTaint: true,
					useCORS: true
				}).then(function(canvas) {
					let box = document.createElement('div')
					let div = document.createElement('div')
					let ctx = canvas.getContext('2d')
					let img = new Image();
					let p = document.createElement('p')
					p.innerHTML = '长按保存图片'
					img.src = canvas.toDataURL('image/png')
					// console.log(img);
					div.className = 'share-picture'
					box.className = 'share-box'
					box.appendChild(div)
					div.appendChild(img)
					div.appendChild(p)
					document.body.appendChild(box);
					box.addEventListener('click', function() {
						this.remove()
					})
					box.addEventListener('wheel', function() {
						this.remove()
					})
					box.addEventListener('touchmove', function() {
						this.remove()
					})
				});
			}
		},
		created() {
			// 获取文章详情
			let userInfo = JSON.parse(localStorage.getItem('userInfo'));
			if (null === userInfo) {
				// return
			} else if (userInfo.isLogin) {
				this.$store.state.isLogin = userInfo.isLogin
			}
			console.log(this.$store.state.isLogin);
			if (this.$store.state.isLogin) {
				this.uid = userInfo.uid
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_detail', {
					article_id: this.$route.query.article_id,
					user_id: this.uid
				}).then(res => {
					console.log(res.data);
					this.item = res.data.data
					this.content = res.data.data.content
					this.title = res.data.data.title
					this.imageSrc = res.data.data.imageSrc
					this.time = res.data.data.time
					this.author = res.data.data.author
					this.like = res.data.data.like
					this.comment = res.data.data.comment
					this.fav = res.data.data.fav
					this.avatar = res.data.data.avatar
					this.is_fav = res.data.data.is_fav
					this.is_like = res.data.data.is_like
				}).catch(err => {
					console.log(err);
				})
			} else if (!this.$store.state.isLogin) {
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_detail', {
					article_id: this.$route.query.article_id,
				}).then(res => {
					console.log(res.data);
					this.item = res.data.data
					this.content = res.data.data.content
					this.title = res.data.data.title
					this.imageSrc = res.data.data.imageSrc
					this.time = res.data.data.time
					this.author = res.data.data.author
					this.like = res.data.data.like
					this.comment = res.data.data.comment
					this.fav = res.data.data.fav
					this.avatar = res.data.data.avatar
					this.is_fav = res.data.data.is_fav
					this.is_like = res.data.data.is_like
				}).catch(err => {
					console.log(err);
				})
			}


		}
	}
</script>

<style lang="less" scoped>
	/deep/ .van-nav-bar {
		background-color: #07c160;
	}

	/deep/.van-nav-bar .van-icon {
		color: #fff;
	}

	/deep/ .van-nav-bar__title {
		color: #fff;
	}

	.van-nav-bar {
		width: 100%;
		position: fixed;
		left: 0;
		top: 0;
	}

	.content {
		margin-bottom: 20px;
		margin-top: 60px;
	}

	.content>h3 {
		text-align: left;
		margin-left: 20px;
		margin-top: 20px;
		margin-bottom: 20px;
	}

	.info {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-start;
		margin-left: 10px;
	}

	.user {
		display: flex;
		margin-left: 20px;
		justify-content: space-between;
		align-items: center;
	}



	.guanzhu {
		border: 1px solid #ccc;
		height: 8vw;
		line-height: 8vw;
		width: 13.33333vw;
		text-align: center;
		font-size: 3.73333vw;
		margin-top: 1px;
		background-color: #fff;
		color: #000;
	}

	.likeit {}
</style>
