<template>
	<div>
		<!-- header -->
		<van-row class="top" style="background-color: #07c160;">
			<van-col span="2"></van-col>
			<router-link to="/searchs">
				<van-col span="20" class="search">
					<div class="search-btn">
						<van-icon name="search" class="search-icon" />
						搜索
					</div>
				</van-col>
			</router-link>
			<van-col span="2"></van-col>
		</van-row>
		<!-- nav -->
		<div class="home_tabs" style="z-index: 999;">
			<van-tabs color="#07c160" @click="getId">
				<van-tab v-for="index in list" :title="index.name" :key="list.id">
				</van-tab>
			</van-tabs>
		</div>
		<list></list>
		<!-- footer -->

	</div>
</template>

<script>
	import {
		Tab,
		Tabs
	} from 'vant';
	import axios from "axios";
	import list from '@/views/homepage/list.vue'
	export default {
		components: {
			[Tabs.name]: Tabs,
			[Tab.name]: Tab,
			list
		},
		data() {
			return {
				value: '',
				active: 0,
				list: [],
				name: 'Home'
			}
		},
		methods: {
			getId(index) {
				this.$bus.$emit('creatId', this.list[index]._id)
				// console.log(this.list[index]._id);
			}
		},
		created() {
			axios.get("https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_cate_list")
				.then((data) => {
					console.log(data);
					this.list = data.data.data
					// console.log(this.list);
				})
		},
		mounted() {}
	}
</script>

<style lang="less" scoped>
	.top {
		width: 100%;
		height: 40px;
		position: fixed;
		top: 0;
	}

	.search-btn {
		margin-top: 5px;
		height: 30px;
		background: #66d79d;
		border: none;
		border-radius: 32px;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #fff;
		font-size: 14px;

		.search-icon {
			margin-right: 5px;
		}
	}

	.home_tabs {
		margin-bottom: 80px;
		width: 100%;
		height: 44px;
		position: fixed;
		top: 40px;
		left: 0px;

		/deep/.van-tab {
			border-bottom: 1px solid #ccc;
			border-right: 1px solid #ccc;
		}

		/deep/ .van-tabs__nav--complete {
			padding-left: 0;
			padding-right: 0;
		}

		/deep/ .van-tabs__line {
			bottom: 25px;
			width: 20px;
		}

	}
</style>
