<template>
	<div class="app">
		<div>
			<van-nav-bar title="个人信息" left-arrow @click-left='retop' />
		</div>
		<div class="list">
			<!-- 头像 -->
			<div class="avatar">
				<van-cell is-link @click="photoshowPopup" style="line-height: 50px;">头像
					<img :src="avatar" alt="" srcset="">
				</van-cell>
			</div>
			<van-popup v-model="photoshow">
				<van-uploader v-model="fileList" :max-count="1" :after-read="afterRead" />
				<h6>点击选择图片</h6>
				<van-button type="primary" size="mini" @click="getphoto">确定</van-button>
				<van-button type="primary" size="mini" @click="photoshow=false">取消</van-button>
			</van-popup>
			<!-- 昵称 -->
			<van-cell title="昵称" is-link @click="usernameshowPopup" :value="oldname"></van-cell>
			<van-popup v-model="usernameshow" style="width: 300px;">
				<van-field v-model="newname" label="用户名" placeholder="请输入新的用户名" />
				<van-button type="primary" size="mini" @click="getname">确定</van-button>
				<van-button type="primary" size="mini" @click="usernameshow=false">取消</van-button>
			</van-popup>
			<!-- 性别 -->
			<van-cell title="性别" is-link :value="sex" @click="sexshowPopup"></van-cell>
			<van-popup v-model="sexshow">
				<van-radio-group v-model="newsex" direction="horizontal" style="margin-top: 40px;margin-left: 50px;">
					<van-radio name="男">男</van-radio>
					<van-radio name="女">女</van-radio>
				</van-radio-group>
				<van-button type="primary" size="mini" @click="getsex">确定</van-button>
				<van-button type="primary" size="mini" @click="sexshow=false">取消</van-button>
			</van-popup>
			<!-- 生日 -->
			<van-cell title="生日" is-link :value="birthday" @click="birthdayshowPopup"></van-cell>
			<van-popup v-model="birthdayshow" style="width:300px;height: 300px;">
				<van-datetime-picker v-model="currentDate" type="date" title="选择年月日" :min-date="minDate"
					:max-date="maxDate" @confirm='confirm' @cancel='cancel' />
			</van-popup>
		</div>
		<van-button type="primary" size="large" @click="changeUser" style="width: 80%;">保存</van-button>
	</div>
</template>

<script>
	import axios from 'axios';
	import {
		Popup,
		Uploader,
		Field,
		Toast,
		RadioGroup,
		Radio,
		DatetimePicker
	} from 'vant';
	import {
		nanoid
	} from 'nanoid'
	export default {
		data() {
			return {
				name: 'user',
				photoshow: false,
				usernameshow: false,
				sexshow: false,
				birthdayshow: false,
				fileList: [],
				uid: '',
				oldname: '',
				newname: '',
				sex: '',
				birthday: '',
				newsex: '',
				minDate: new Date(1980, 0, 1),
				maxDate: new Date(2023, 10, 1),
				currentDate: '',
				avatar: '',
				key: ''
			}
		},
		components: {
			[Popup.name]: Popup,
			[DatetimePicker.name]: DatetimePicker,
			[Uploader.name]: Uploader,
			[Field.name]: Field,
			[RadioGroup.name]: RadioGroup,
			[Radio.name]: Radio,
		},
		methods: {
			retop() {
				this.$router.go(-1)
			},
			// 显示隐藏弹出层
			photoshowPopup() {
				this.photoshow = true;
			},
			usernameshowPopup() {
				this.usernameshow = true;
			},
			sexshowPopup() {
				this.sexshow = true;
			},
			birthdayshowPopup() {
				this.birthdayshow = true;
			},
			// 获取新昵称
			getname() {
				this.oldname = this.newname
				this.usernameshow = false
			},
			// 获取新性别
			getsex() {
				this.sex = this.newsex
				this.sexshow = false
			},
			// 获取生日
			confirm(value) {
				this.birthday = value.toLocaleDateString()
				this.birthdayshow = false
			},
			cancel() {
				this.birthdayshow = false
			},
			// 获取头像
			getphoto() {
				this.avatar = `http://toutiao.longxiaokj.com/${this.key}`
				this.photoshow = false
			},
			afterRead(file) {
				axios.get('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/upload/token').then(res => {
					// console.log(res.data);
					let FD = new FormData();
					FD.append('key', nanoid());
					FD.append('token', res.data.token);
					FD.append('file', file.file);
					axios.post('https://upload-z1.qiniup.com',
						FD
					).then(res => {
						// console.log(res.data.key);
						this.key = res.data.key
					}).catch(err => {
						console.log(err);
					})
				})
			},
			// 提交新信息
			changeUser() {
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/editUserInfo', {
					uid: this.uid,
					avatar: this.avatar,
					nickname: this.oldname,
					birthday: this.birthday,
					sex: this.sex
				}).then(res => {
					console.log(res.data);
					if (res.data.code === 30011) {
						setTimeout(() => {
							Toast('您或许没有更改任何信息')
						}, 200)
					}
					if (res.data.msg === '更新成功') {
						Toast(res.data.msg)
						localStorage.clear();
						let userInfo = {
							isLogin: false
						};
						window.localStorage.setItem("userInfo", JSON.stringify(userInfo));
						this.$store.state.isLogin = userInfo.isLogin
						setTimeout(() => {
							this.$router.push({
								path: '/my'
							}, () => {}, () => {}); //退出登录后1秒后跳转至首页
							Toast('请重新登录')
						}, 1000)
					}
				}).catch(err => {
					console.log(err);
				})
			}
		},
		mounted() {
			let userInfo = JSON.parse(localStorage.getItem('userInfo'));
			this.uid = userInfo.uid
			this.oldname = userInfo.nickname
			this.sex = userInfo.sex
			this.birthday = userInfo.birthday
			this.avatar = userInfo.avatar
		}
	}
</script>

<style lang="less" scoped>
	.app {
		height: 100vh;
		background-color: #f7f7f7;
	}

	/deep/ .van-nav-bar {
		background-color: #07c160;
	}

	/deep/.van-nav-bar .van-icon {
		color: #fff;
	}

	/deep/ .van-nav-bar__title {
		color: #fff;
	}

	.van-button {
		margin-top: 30px;
	}

	.activr .van-cell__right-icon {
		line-height: 30px;
	}

	.van-cell {
		align-items: center;
		text-align: left;
	}

	img {
		width: 40px;
		height: 40px;
		border-radius: 20px;
		background-color: red;
		float: right;
		margin-top: 5px;
	}

	.van-popup--center {
		width: 200px;
		height: 200px;
		text-align: center;
	}

	.van-uploader__upload {
		margin: 0;
	}

	.van-uploader {
		margin-top: 20px;
		margin-bottom: 10px;
	}
</style>
