<template>
	<div>
		<div>
			<van-nav-bar title="发布" />
		</div>
		<div class="from">
			<!-- 标题内容 -->
			<van-form>
				<van-field v-model="title" name="title" placeholder="请输入标题" label="标题"
					:rules="[{ required: true, message: '请输入标题' }]" />
				<van-field v-model="content" type="text" name="content" placeholder="请输入内容" label="内容"
					:rules="[{ required: true, message: '请输入内容' }]" />
			</van-form>
			<!-- 选择分类 -->
			<van-field readonly clickable label="分类" :value="value" placeholder="请选择分类" @click="showPicker = true" />
			<van-popup v-model="showPicker" round position="bottom">
				<van-picker show-toolbar :columns="columns" @cancel="showPicker = false" @confirm="onConfirm" />
			</van-popup>
			<!-- 上传图片 -->
			<van-field name="uploader" style="margin-top: 10px;">
				<template #input>
					<van-uploader :after-read="afterRead" v-model="fileList" :max-count="3" />
				</template>
			</van-field>
			<div style="margin: 16px;">
				<van-button round block type="info" native-type="submit" color="#07c160" @click="onSubmit">提交
				</van-button>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	import {
		Field,
		Form,
		Uploader,
		Picker,
		Popup,
		Toast
	} from 'vant';
	import {
		nanoid
	} from 'nanoid'
	export default {
		data() {
			return {
				name: 'publish',
				title: '',
				text: '',
				content: '',
				uid: '',
				username: '',
				value: '',
				showPicker: false,
				columns: ['游戏专区', '婚恋交友', '招聘求职', '装修装饰', '明日头条', '今日头条', '劳力市场'],
				token: '',
				key: [],
				fileList: [],
				cate_id: '',
				show: false,
				type: ''
			}
		},
		components: {
			[Field.name]: Field,
			[Form.name]: Form,
			[Uploader.name]: Uploader,
			[Picker.name]: Picker,
			[Popup.name]: Popup,
		},
		methods: {
			afterRead(file) {
				axios.get('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/upload/token').then(res => {
					// console.log(res.data);
					this.token = res.data.token
					let type = file.file.type.split('/')[1]
					let FD = new FormData();
					// FD.append('key', 'http://toutiao.longxiaokj.com/' + nanoid() + '.' + type);
					FD.append('key', nanoid() + '.' + type);
					FD.append('token', this.token);
					FD.append('file', file.file);
					axios.post('https://upload-z1.qiniup.com',
						FD
					).then(res => {
						console.log(res.data);
						this.key = res.data.key
						// this.key.push(res.data.key)
						console.log(this.key);
					}).catch(err => {
						console.log(err);
					})
				})
			},
			onSubmit() {
				if (this.value) {
					axios.get('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_cate_list').then(
						res => {
							// console.log(res.data.data);
							let obj = res.data.data.find(val => val.name === this.value)
							// console.log(obj._id);
							this.cate_id = obj._id
						})
				}
				if (this.title == '') {
					Toast('标题不能为空')
					return
				}
				if (this.content == '') {
					Toast('内容不能为空')
					return
				}
				if (this.value == '') {
					Toast('分类不能为空')
					return
				}
				if (this.fileList == '') {
					Toast('图片不能为空')
					return
				}
				let timer = setInterval(() => {
						this.show = true
						Toast('发布中...')
						if (this.key) {
							axios.post(
								'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_article', {
									title: this.title,
									content: this.content,
									cate_id: this.cate_id,
									cate_name: this.value,
									author: this.username,
									author_id: this.uid,
									// imageSrc: this.key,
									imageSrc: [`http://toutiao.longxiaokj.com/${this.key}`]
								}).then(res => {
								// console.log(this.key);
								clearInterval(timer)
								this.show = false
								// console.log(this.cate_id);
								console.log(res.data);
								if (res.data.code === 20011) {
									Toast(res.data.msg)
								}
								if (res.data.code === 0) {
									setTimeout(() => {
										Toast('发布成功')
										this.title = ''
										this.content = ''
										this.cate_id = ''
										this.value = ''
										this.username = ''
										this.uid = ''
										this.fileList = []
									}, 500)
								}
							}).catch(err => {
								Toast(err)
							})
						}
					},
					200)

			},
			onConfirm(value) {
				this.value = value;
				this.showPicker = false;
			},
		},
		mounted() {
			// 获取id
			let userInfo = JSON.parse(localStorage.getItem('userInfo'));
			console.log(userInfo);
			this.uid = userInfo.uid
			this.username = userInfo.username

		}
	}
</script>

<style lang="less" scoped>
	/deep/ .van-nav-bar {
		background-color: #07c160;
	}

	/deep/ .van-nav-bar__title {
		color: #fff;
	}
</style>
