import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import timefrom from '@/utils/timeform'
import VueClipboard from 'vue-clipboard2'
import {
	Icon,
	Tabbar,
	TabbarItem,
	Col,
	Row,
	Button,
	PullRefresh,
	Cell,
	NavBar,
	Search
} from 'vant';

Vue.use(VueClipboard)
Vue.use(Tabbar);
Vue.use(Search);
Vue.use(TabbarItem);
Vue.use(Icon);
Vue.use(Col);
Vue.use(Cell);
Vue.use(NavBar);
Vue.use(Row);
Vue.use(Button);
Vue.use(PullRefresh);
// axios.defaults.baseURL = "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http"
Vue.prototype.$axios = axios
Vue.config.productionTip = false




new Vue({
	router,
	store,
	render: h => h(App),
	beforeCreate() {
		Vue.prototype.$bus = this
	}
}).$mount('#app')
