<template>
	<div>
		<div class="footer">
			<!-- 评论 -->
			<div class="input">
				<van-icon name="edit" style="position: absolute;top: 2.13333vw;left: 2.13333vw;" />
				<input type="text" placeholder="写评论..." @keyup.enter="add" v-model="value">
			</div>
			<div class="icon" style="position: relative;">
				<van-icon name="chat-o" class="icons" @click="add" />
				<!-- 收藏 -->
				<van-icon name="star" class="icons" v-if="is_fav" color="#ee0a24" @click="cancelfav" />
				<van-icon name="star" class="icons" @click="likefav" v-else />
				<!-- 点赞 -->
				<van-icon name="good-job" class="icons" v-if="is_like" color="#ee0a24" @click="cancellikeit" />
				<van-icon name="good-job" class="icons" @click="likeit" v-else />
				<!-- 分享 -->
				<van-icon name="share-o" class="icons" @click="onShare" />
			</div>
		</div>
		<van-share-sheet v-model="showShare" title="立即分享给好友" :options="options" @select="onSelect" />
	</div>
</template>

<script>
	import axios from 'axios'
	import qrcode from 'qrcode'
	import {
		ShareSheet,
		Toast
	} from 'vant';
	export default {
		components: {
			[ShareSheet.name]: ShareSheet
		},
		props: ['uid', 'item'],
		data() {
			return {
				is_fav: false,
				is_like: false,
				value: '',
				commentlist: '',
				showShare: false,
				dataUrl: '',
				options: [
					[{
							name: '微信',
							icon: 'wechat'
						},
						{
							name: '微博',
							icon: 'weibo'
						},
						{
							name: 'QQ',
							icon: 'qq'
						},
					],
					[{
							name: '复制链接',
							icon: 'link'
						},
						{
							name: '分享海报',
							icon: 'poster'
						},
						{
							name: '二维码',
							icon: 'qrcode'
						},
					],
				],
			};
		},
		methods: {
			// 分享
			onSelect({
				name
			}) {
				this.showShare = false;
				let onShare = {
					'微博': this.shareWeibo,
					'微信': this.shareWx,
					'QQ': this.shareQq,
					'复制链接': this.shareLink,
					'二维码': this.shareWx,
					'分享海报': this.poster
				}
				onShare[name]()
			},
			shareWeibo() {
				let url = 'https://service.weibo.com/share/share.php'
				let title = this.item.title + location.href + '#头条#'

				let pic = this.item.imageSrc[0] || '@/assets/doge.png'
				window.open(url + '?title=' + encodeURIComponent(title) + '&pic=' + encodeURIComponent(pic))
			},
			shareWx() {
				qrcode.toDataURL(location.href).then(url => {
					console.log(url);
					let image = new Image()
					image.src = url
					let div = document.createElement('div')
					let box = document.createElement('div')
					box.className = 'share-box'
					div.className = 'share-picture'
					let p = document.createElement('p')
					p.innerHTML = '长按扫描二维码'
					box.appendChild(div)
					div.appendChild(image)
					div.appendChild(p)
					document.body.appendChild(box)
					box.addEventListener('click', function() {
						this.remove()
					})
					box.addEventListener('wheel', function() {
						this.remove()
					})
					box.addEventListener('touchmove', function() {
						this.remove()
					})
				})
			},
			shareQq() {
				console.log(1);
				let url = 'https://connect.qq.com/widget/shareqq/index.html'
				let title = this.item.title + '欢迎来到莱头条 #头条#'
				window.open(url + '?url=' + location.href + '&title=' + encodeURIComponent(title) + '&pics=' +
					encodeURIComponent(this.item.imageSrc[0]) + '&site=' + '莱头条')
			},
			shareLink() {
				this.$copyText(location.href).then(
					res => {
						Toast('已成功复制到剪切板')
					},
					err => {
						console.log(err)
					}
				)
			},
			poster() {
				this.$emit('render')
			},
			// 收藏
			likefav() {
				if (this.uid === '') {
					Toast('您还未登录')
				}
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_fav', {
					user_id: this.uid,
					article_id: this.$route.query.article_id,
				}).then(res => {
					// console.log(res.data);
					if (res.data.code === 0) {
						Toast(res.data.msg);
						this.is_fav = true;
					}
				})
			},
			// 取消收藏
			cancelfav() {
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/remove_fav', {
					user_id: this.uid,
					article_id: this.$route.query.article_id,
				}).then(res => {
					// console.log(res.data);
					if (res.data.code === 0) {
						Toast(res.data.msg);
						this.is_fav = false;
					}
				})
			},
			// 点赞
			likeit() {
				if (this.uid === '') {
					Toast('您还未登录')
				}
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/like', {
					user_id: this.uid,
					article_id: this.$route.query.article_id,
				}).then(res => {
					// console.log(res.data);
					if (res.data.code === 0) {
						Toast(res.data.msg);
						this.is_like = true;
					}
				})
			},
			// 取消点赞
			cancellikeit() {
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/unlike', {
					user_id: this.uid,
					article_id: this.$route.query.article_id,
				}).then(res => {
					// console.log(res.data);
					if (res.data.code === 0) {
						Toast(res.data.msg);
						this.is_like = false;
					}
				})
			},
			// 评论文章
			add() {
				if (this.uid === '') {
					Toast('您还未登录')
				}
				axios.post('https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_comment', {
					user_id: this.uid,
					article_id: this.$route.query.article_id,
					content: this.value,
					comment_type: 0
				}).then(res => {
					console.log(res.data);
					if (res.data.code === 0) {
						this.$bus.$emit('getcomment', this.commentlist)
						Toast(res.data.msg)
						this.value = ''
					}
				})
			},
			// 分享
			onShare() {
				this.showShare = !this.showShare
			},
		},
	};
</script>

<style lang="less" scoped>
	.footer {
		display: flex;
		padding: 2.66667vw 4.26667vw;
		position: fixed;
		bottom: -1px;
		left: 0;
		background-color: #fff;
		border-top: 1px solid #f4f4f4;
	}

	.footer .input {
		position: relative;
		flex: 4;
		display: flex;
	}

	.input>input {
		width: 100%;
		background-color: #f3f3f3;
		padding: 2.13333vw 0 2.13333vw 9.33333vw;
		border-radius: 4vw;
		outline: none;
		border: none;
	}

	.footer .icon {
		text-align: center;
		height: 9.33333vw;
		line-height: 11.33333vw;
	}

	.footer .icon .icons {
		margin: 0 4.26667vw 2.66667vw 2.66667vw;
		font-size: 6.4vw;
	}

	/deep/.van-share-sheet__options {
		justify-content: space-around;
	}
</style>
