<template>
	<div>
		<div>
			<van-nav-bar title="浏览历史" left-arrow @click-left="onClickLeft" />
		</div>
		<div class="lists" style="margin-top: 50px;">
			<ul>
				<li v-for="item in Array.prototype.reverse.call(list)" :key="item.id" style="color: #000;"
					@click="todetails(item)">
					<div class="top">
						<p style="word-wrap: break-word;width: 30vw;">{{item.title}}</p>
						<div class="img" style="width:110px;height: 100px;">
							<img :src="item.imageSrc[0]" v-show="item.imageSrc.length"
								style="margin-top: 10px;width: 100%;height: 100%;object-fit: cover;">
						</div>
					</div>
					<div class="bottom">
						<span style="margin-left: 10px;" class="van-ellipsis">{{item.author}} </span>
						<div class="bottom-right" style="margin-right: 10px;">
							<span>{{item.comment}}评论</span>
							<span> 发布于{{new Date(item.time).toLocaleDateString()}}</span>
						</div>
					</div>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';
	export default {
		data() {
			return {
				name: 'historylist',
				uid: '',
				list: '',
			}
		},
		methods: {
			// 返回上一级路由
			onClickLeft() {
				this.$router.back()
			},
			// 详情
			todetails(item) {
				this.$router.push({
					path: '/details',
					query: {
						article_id: item._id
					}
				})
			}
		},
		created() {
			let userInfo = JSON.parse(localStorage.getItem('userInfo'));
			this.uid = userInfo.uid
			axios.get(
				`https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_history_list?uid=${this.uid}&skip&limit`
			).then(res => {
				console.log(res.data);
				this.list = res.data.data
			})
		}
	}
</script>

<style lang="less" scoped>
	.van-nav-bar {
		background-color: #07c160;
	}

	.van-nav-bar {
		width: 100%;
		position: fixed;
		left: 0;
		top: 0;
	}

	/deep/ .van-nav-bar__title {
		color: #fff;
	}

	/deep/ .van-nav-bar .van-icon {
		color: #fff;
	}

	li {
		display: flex;
		flex-direction: column;
		border-bottom: 1px solid #eee;
	}

	.top {
		display: flex;
		justify-content: space-around;
		height: 120px;
		margin-top: 5px;
		margin-bottom: 10px;
	}

	.top>p {
		flex: 6;
		text-align: left;
		margin-left: 10px;
		margin-top: 10px;
	}

	.top .img {
		flex: 4;
		margin-right: 10px;
		margin-bottom: 10px;
	}

	.bottom {
		display: flex;
		justify-content: space-between;
		margin-bottom: 10px;
	}
</style>
