import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/components/login.vue'
import Home from '@/views/homepage/home.vue'
import publish from '@/views/publish.vue'
import my from '@/views/userInfo/my.vue'
import user from '@/views/userInfo/user.vue'
import searchs from '@/views/search.vue'
import details from '@/views/article/details.vue'
import mylimit from '@/views/userInfo/mylimit.vue'
import fav from '@/views/userInfo/fav.vue'
import historylist from '@/views/userInfo/historylist.vue'

Vue.use(VueRouter)

const routes = [{
		// store,
		path: '/',
		name: 'Home',
		component: Home,
		meta: {
			root: true,
		},
		component: () => import('@/views/homepage/home.vue')
	},
	{
		path: '/Login',
		name: 'Login',
		component: Login,
		meta: {

		},
		component: () => import('@/components/login.vue')
	},
	{
		path: '/publish',
		name: 'publish',
		component: publish,
		meta: {
			root: true,
			isLogin: true
		},
		component: () => import('@/views/publish.vue')
	},
	{
		path: '/my',
		name: 'my',
		component: my,
		meta: {
			root: true,
			isLogin: true
		},
		component: () => import('@/views/userInfo/my.vue')
	},
	{
		path: '/user',
		name: 'user',
		component: user,
		meta: {
			isLogin: false
		},
		component: () => import('@/views/userInfo/user.vue')
	},
	{
		path: '/searchs',
		name: 'searchs',
		component: searchs,
		meta: {
			isLogin: false
		},
		component: () => import('@/views/search.vue')
	},
	{
		path: '/details',
		name: 'details',
		component: details,
		meta: {
			isLogin: false
		},
		component: () => import('@/views/article/details.vue')
	},
	{
		path: '/mylimit',
		name: 'mylimit',
		component: mylimit,
		meta: {
			isLogin: false
		},
		component: () => import('@/views/userInfo/mylimit.vue')
	},
	{
		path: '/fav',
		name: 'fav',
		component: fav,
		meta: {
			isLogin: false
		},
		component: () => import('@/views/userInfo/fav.vue')
	},
	{
		path: '/historylist',
		name: 'historylist',
		component: historylist,
		meta: {
			isLogin: false
		},
		component: () => import('@/views/userInfo/historylist.vue')
	},
]

const router = new VueRouter({
	// mode: 'history',
	base: process.env.BASE_URL,
	routes
})

export default router
